body {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  margin-top: 200px;
  width: 500px;
  height: 200px;
  background-color: rgb(6, 41, 67);
}
form {
  background-color: #15172b;
  border-radius: 50px;
  height: 320px;
  padding: 20px;
  width: 600px;
}
.value-input {
  background-color: #303245;
  border-radius: 12px;
  border: 0;
  box-sizing: border-box;
  color: #eee;
  font-size: 18px;
  height: 100%;
  outline: 0;
  padding: 4px 20px 0;
  width: 100%;
}
.result {
  color: #fff;
  display: flex;
  justify-content: space-evenly;
  margin-top: 4rem;
  font-size: 2rem;
}
.drop-down {
  color: #fff;
  display: flex;
  justify-content: space-evenly;
  margin-top: 2rem;
  font-size: 2rem;
}
.selector {
  background-color: #303245;
  border-radius: 10px;
  width: 10rem;
  color: whitesmoke;
  text-align: center;
}
select {
  appearance: none;
}
.input-container {
  height: 30px;
  position: relative;
  width: 100%;
}
.ic1 {
  margin-top: 30px;
}
.ic2 {
  margin-top: 20px;
}
.input {
  background-color: #303245;
  border-radius: 12px;
  border: 0;
  box-sizing: border-box;
  color: #eee;
  font-size: 18px;
  height: 100%;
  outline: 0;
  padding: 4px 20px 0;
  width: 100%;
}
.submit {
  background-color: rgb(6, 41, 67);
  border-radius: 12px;
  border: 0;
  box-sizing: border-box;
  color: #eee;
  cursor: pointer;
  font-size: 18px;
  height: 50px;
  margin-top: 38px;
  text-align: center;
  width: 100%;
}
